<template    >
  <div class="position-relative scene" ref="scene"   >
    <div v-if="!flag"
      class="
        position-absolute
        w-100
        h-100
        bg-dark
        d-flex
        justify-content-center
        align-items-center
        
      "
    >
      <div class="w-50">
        <p class="lead text-light mb-2">载入中</p>
        <div class="progress">
          <div
            class="bg-info text-dark"
            role="progress"
            :style="`width:${progress}%;`"
          >
            {{ progress}}%
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as THREE from 'three';
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader.js";
import {
  EffectComposer,
  RenderPass,
  EffectPass,
  BloomEffect,
} from "postprocessing";
export default {
  name: "Scene",
  data() {
    return {
      publicPath: process.env.BASE_URL,
      progress: 0,
      flag:false,
    };
  },
  methods: {
    init() {
      //   let this.scene, this.camera, this.renderer, this.composer;

      this.scene = new THREE.Scene();
      this.scene.background = new THREE.Color(0x000000);
      // Renderer
      this.renderer = new THREE.WebGLRenderer({ antialias: true });
      this.renderer.setSize(
        this.$refs.scene.clientWidth,
        this.$refs.scene.clientHeight
      );
      this.renderer.toneMapping = THREE.ReinhardToneMapping;
      this.renderer.toneMappingExposure = 3;
      this.renderer.shadowMap.enabled = true;
      this.$refs.scene.appendChild(this.renderer.domElement);
      // Camera
      const aspect =
        this.$refs.scene.clientWidth / this.$refs.scene.clientHeight;
      // this.camera = new PerspectiveCamera(60, aspect, 0.01, 1000);
      this.camera = new THREE.PerspectiveCamera(15, aspect, 0.1, 1000);
            // this.camera = new THREE.PerspectiveCamera(60, aspect, 0.1, 2000);
      this.camera.rotation.y = (90 / 180) * Math.PI;
      this.camera.position.set(-20, 60,-150);
      // this.camera.position.set(0.5, 0, 0);
      // this.camera.position.set(0, 3, 5);
      // Camera Controls
      this.controls = new OrbitControls(this.camera, this.renderer.domElement);
      // controls.addEventListener("change", this.renderer);
      this.controls.update();
      // Light
      const hemiLight = new THREE.HemisphereLight(0xffeeb1, 0x080820, 2);
      this.scene.add(hemiLight);
      this.spotLight = new THREE.SpotLight(0xffa95c, 4);
      this.spotLight.castShadow = true;
      this.spotLight.shadow.bias = -0.0001;
      this.spotLight.shadow.mapSize.width = 10000;
      this.spotLight.shadow.mapSize.height = 10000;

      this.scene.add(this.spotLight);

      // Post Processing
      this.composer = new EffectComposer(this.renderer);
      this.composer.addPass(new RenderPass(this.scene, this.camera));
      const effectPass = new EffectPass(
        this.camera,
        new BloomEffect({
          intensity: 3,
          luminanceThreshold: 8.8,
          width: 100,
          height: 100,
        })
      );
      this.composer.addPass(effectPass);

      // Loading Manager
      const manager = new THREE.LoadingManager();
      manager.onProgress = (url, itemsLoaded, itemsTotal) => {
        this.progress = (itemsLoaded / itemsTotal) * 100;
        if ((itemsLoaded / itemsTotal) * 100 === 100) {
          console.log("ddd")
          setTimeout(() => {
          this.flag = true;
          }, 1000);
        }
      };

      // Loader
      const loader = new GLTFLoader(manager);
      loader.load(`${this.publicPath}models/miku/scene.gltf`, (result) => {
        const model = result.scene.children[0];
        model.scale.set(1,1,1);
        model.translateY(20);
        model.traverse((n) => {
          if (n.isMesh) {
            n.receiveShadow = true; 
            n.castShadow = true;
            if (n.material.map) n.material.map.anisotropy = 100;
          }
        });

        this.scene.add(model);
        this.animate();
      });
    },
    animate() {
      requestAnimationFrame(this.animate);
      this.controls.update();
      this.spotLight.position.set(
        this.camera.position.x + 5,
        this.camera.position.y + 5,
        this.camera.position.z + 5
      );
      // this.renderer.render(this.scene, this.camera);
      this.composer.render();
    },
    onCanvasResize() {
      this.camera.aspect =
        this.$refs.scene.clientWidth / this.$refs.scene.clientHeight;
      this.camera.updateProjectionMatrix();
      this.renderer.setSize(
        this.$refs.scene.clientWidth,
        this.$refs.scene.clientHeight
      );
      this.composer.setSize(
        this.$refs.scene.clientWidth,
        this.$refs.scene.clientHeight
      );
    },
  },

  mounted() {
    
    window.addEventListener("resize", this.onCanvasResize, false);

    this.init();
  },
};
</script>
<style scoped>
.scene {
  width: 70%;
  height: 0;
  padding-bottom: 39.375%;
}
.w-20 {
  width: 20%;
}
.h-smaill{
    height: 10px;
}

@media (max-width: 992px) {
  .scene {
    width: 100%;
    height: 0;
    padding-bottom: 56.25%；;
  }
}
</style>
